import { useEffect, useState } from "react";
import Content from "./Content";
import Menu from "./Menu";
import "./sass/App.scss";
import AnimatedCursor from "react-animated-cursor";
import { motion, useAnimation } from "framer-motion";

function App() {
  const [cursorColor, setCursorColor] = useState("0, 0, 0");
  const [backgroundColor, setBackgroundColor] = useState("256, 256, 256");

  const controls = useAnimation();

  useEffect(() => {
    controls.start({
      backgroundColor: `rgb(${backgroundColor})`,
      // backgroundImage: `linear-gradient(to bottom, rgb(${backgroundColor}), rgb(${backgroundColor}, 0.7))`,
      transition: {
        duration: 0.4,
      },
    });
  }, [controls, backgroundColor]);

  return (
    <div className="App">
      <motion.div animate={controls}>
        <AnimatedCursor color={cursorColor} />
        <div className="Wrapper">
          <Menu />
          <Content
            setCursorColor={setCursorColor}
            setBackgroundColor={setBackgroundColor}
          />
          <p className="scroll-down">Scroll Down</p>
          <p className="tagline">Visual artist and photographer</p>
        </div>
      </motion.div>
    </div>
  );
}

const PARAMS = {
  particles: {
    number: {
      value: 60,
      density: {
        enable: true,
        value_area: 1500,
      },
    },
    line_linked: {
      enable: true,
      opacity: 0.02,
    },
    move: {
      direction: "right",
      speed: 0.05,
    },
    size: {
      value: 1,
    },
    opacity: {
      anim: {
        enable: true,
        speed: 1,
        opacity_min: 0.05,
      },
    },
  },
  interactivity: {
    events: {
      onclick: {
        enable: true,
        mode: "push",
      },
    },
    modes: {
      push: {
        particles_nb: 1,
      },
    },
  },
  retina_detect: true,
};

export default App;
